/* Four column with gap */
.four-column-gap {
    @include desktop-up {
        @include grid(1fr 1rem 1fr 1rem 1fr 1rem 1fr);
    }

    @include tablet-only {
        @include grid(1fr 1rem 1fr, auto 1rem auto);
    }

    @include mobile-only {
        @include grid(1fr, auto 1rem auto 1rem auto 1rem auto);
    }

    > *:nth-child(1) {
        @include grid;
        @include align(start);

        @include desktop-up {
            @include grid-start(1);
        }
        
        @include tablet-only {
            @include grid-start(1);
        }

        @include mobile-only {
            @include grid-start(1);
        }
    }

    > *:nth-child(2) {
        @include grid;
        @include align(start);

        @include desktop-up {
            @include grid-start(3);
        }
        
        @include tablet-only {
            @include grid-start(3, 1);
        }

        @include mobile-only {
            @include grid-start(1, 3);
        }
    }

    > *:nth-child(3) {
        @include grid;
        @include align(start);

        @include desktop-up {
            @include grid-start(5);
        }
        
        @include tablet-only {
            @include grid-start(1, 3);
        }

        @include mobile-only {
            @include grid-start(1, 5);
        }
    }

    > *:nth-child(4) {
        @include grid;
        @include align(start);

        @include desktop-up {
            @include grid-start(7);
        }
        
        @include tablet-only {
            @include grid-start(3, 3);
        }

        @include mobile-only {
            @include grid-start(1, 7);
        }
    }
}


